import React, {Component} from "react";
import {
	FormControl, Select, InputNumber
} from "tinper-bee";
import FormList from 'components/FormList';
import PopDialog from 'components/Pop';

import {success, Error} from "utils";
import {actions} from "mirrorx";

import './index.less';


const FormItem = FormList.Item;
const {Option} = Select;

const titleArr = ["", "", ""];

class CommonPop extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    /**
	 * @description modal
	 * @param {Boolean} showModal truefalse
	 * @param {Number} btnFlag 
	 * @returns {*} null
	 */
	onCloseEdit = () => {
        let {comModalParam} = this.props,
            resultObj = {};
        resultObj = Object.assign({}, comModalParam, {
			showModal : false,
			btnFlag : 0,
		});

        actions.walsinTree.updateState({
            comModalParam : resultObj,
            showLoading : false
        })
    }

    /**
	 * @description 
	 * 				search_treeId
	 * @param {string} search_treeId ID
	 * @param {Boolean} showLoading truefalse
	 * @returns {*} null
	 */
	onSubmitEdit = async () => { // modal
        let _this = this;
        _this.props.form.validateFields( async (err, values) => {

            if (err) {
                return ;
            } else {
                await actions.walsinTree.updateState({
                    showLoading : true
                });

                let {comModalParam , paginationParam} = _this.props || {},
                    {initEditValue, btnFlag} = comModalParam || {},
                    {search_treeId} = paginationParam.reqParam || { },
                    resultObj = {};
                console.log("onsave oldData", initEditValue);
                // 
                if( !btnFlag && !search_treeId) {
                    _this.onCloseEdit();
                    actions.walsinTree.updateState({
                        showLoading : false,
                    });
                    Error('');
                    return ;
                }

                // 
                _this.handleIntData(values);

                // id
                resultObj = Object.assign({}, {
                    treeId : search_treeId
                },initEditValue, values);

				if(btnFlag) {
					await actions.walsinTree.addEditData(resultObj);
				} else {
					await actions.walsinTree.addTableData(resultObj);
				}

                _this.onCloseEdit();
            }

        });
    }

    handleIntData = (values) => {
        // inputnumber,
		let numArray = ['age'];

		for(let item of numArray) {
			if(typeof values[item] !== 'undefined') {
            	values[item] = Number(values[item]);
			}
		}
    }

    onHideEdit = () => {
		//iconModal
        this.onCloseEdit();
	}

	/**
	 *
	 * @description 
	 * @param {Number} btnFlag 012
	 * @returns {Array} btns 
	 */
	onHandleBtns = (btnFlag) => {
		let btns = null;

		if (btnFlag == 2) {
			btns = [];
		}else {
			btns = this.defaultBtnConfig
		}

		return btns;
	}

	defaultBtnConfig = [
        {
            label: '',
            fun: this.onCloseEdit,
            shape: 'border'
        },{
            label: '',
            fun: this.onSubmitEdit,
            colors: 'primary'
        }
    ]

    render() {
        let _this = this;

        const {form, comModalParam} = _this.props;
        let {showModal = false, initEditValue = {}, btnFlag = 0} = comModalParam || {}
        const {getFieldProps, getFieldError} = form;
		const {name, sex, age} = initEditValue || {};

		let btns = _this.onHandleBtns(btnFlag);
        return (
			<PopDialog
				show={showModal}
				title={titleArr[btnFlag]}
				size='lg'
				btns={btns}
				close={_this.onCloseEdit}
				>
				<FormList>
                    <FormItem required label={""}>
                        <FormControl
                            disabled = {btnFlag == 2}
                            {...getFieldProps('name', {
                                validateTrigger: 'onBlur',
                                initialValue: name || '',
                                rules: [{
                                    type: 'string',
                                    required: true,
                                    pattern: /\S+/ig,
                                    message: '',
                                }],
                            })}
                        />
                        <span className='error'>{getFieldError('name')}</span>
                    </FormItem>

                    <FormItem required label={""}>
                        <Select
                            disabled = {btnFlag == 2}
                            {...getFieldProps('sex', {
                                initialValue: sex || 1,
                                rules: [{
                                    required: true, message: '',
                                }],
                            })}
                        >
                            <Option value={1}></Option>
                            <Option value={2}></Option>
                        </Select>
                        <span className='error'>{getFieldError('sex')}</span>
                    </FormItem>
                    <FormItem required label={""}>
                        <InputNumber iconStyle="one" min={1} step={1}  max={99}
                                     disabled = {btnFlag == 2}
                                     {...getFieldProps('age', {
                                         initialValue: age ? age : "",
                                         rules: [{
                                           type: 'number',
                                           max: 99,
                                           transform: (value) => {
                                             return value * 1
                                           },
                                           pattern: /^[0-9]+$/,
                                           required: true,
                                           message: "100"
                                         }],
                                     })}
                        />
                        <span className='error'>{getFieldError('age')}</span>
                    </FormItem>
				</FormList>
			</PopDialog>
        )
    }
}

export default FormList.createForm()(CommonPop);
